<script>


export default {
name: 'AboutView',
    data: () => ({
      minDate: "2019-07-04",
      minTime: "",
      title:"",
      descri:"",
      dat:"",
      tim:"",
      dialog_err:false,
      dialog: false,
      editname:false,
      firstName:"",
      lastName:"",
      userName:"",
      email:"",
      time:"",
      date:"",
      loginopen:true,
      logupopen:false,
      check:"",
      oldpassword:"",
      password:"",
      passwordgood:"",
      editpassword:false,
      deletaccount:false,
      register: false,
      reverce:false,
      datatabel:true,
      what:"",
      details: false,
      headers: [
          {text: 'Number' , value: 'number'},
          {
            text: 'Mission Name',
            align: 'start',
            value :"name",
          },
          // { text: 'Descrition', value: 'description' },
          { text: 'Date', value: 'date' },
          { text: 'Time', value: 'time' },
          { text: 'Edit', value: 'edit' },

        ],


      desserts: [

        ],

        nameMission:"",
        descriptionMission:"",
        addmission:false,
        confirmdelet:false,
        d1:false,
        d2:false,
        d3:false,
        d4:false,
        d5:false,
        d6:false,
        d7:false,
        numericdialog:1,
        forgetpass:"",
        forgetemail:false,
        forgetmail:"",
        viewpassword:false,
        yourpassword:'',
        youremail:'',
        viewemail:false, 
    }), 


    //  mounted



    mounted (){

      if(localStorage.getItem('data') != null){
          this.desserts = JSON.parse(localStorage.getItem('data'));
        }
      
        setInterval(() => {
            document.getElementById("tools").style.width = (window.innerWidth - 410) +"px"; 
            if(localStorage.getItem("username") == null){
            do {
              this.register = true;
            } while (this.register == false);
        }
        }, 1000); 
        document.getElementById("name").innerHTML = localStorage.getItem("firstname") + " " + localStorage.getItem("lastname");   

        // date 
         
        
        setInterval(() => {
        var date = new Date();
        document.getElementById("hour").innerHTML = date.getHours() + " : ";
        document.getElementById("min").innerHTML = date.getMinutes() + " : ";
        document.getElementById("second").innerHTML = date.getSeconds() + " ";
        document.getElementById("year").innerHTML = date.getFullYear() + " ";
        document.getElementById("day").innerHTML = date.getDate() + " / ";
        document.getElementById("month").innerHTML = date.getMonth() + 1 + " / ";

        if(this.desserts <= 0 ){
            this.datatabel = false;
        }else{
          this.datatabel = true;
        }




        }, 1000);




        var date = new Date(); 
        this.minDate =  date.getFullYear()+"-0"+parseInt(date.getMonth() + 1)+"-0"+date.getDate();
        this.date = date.getFullYear()+"-0"+parseInt(date.getMonth() + 1)+"-0"+date.getDate();
        this.time = date.getHours() + ":" + date.getMinutes();
        
        
        // Mission finich 
        
        
      
        for(var item in this.desserts){
          this.desserts[item].number = item;
          localStorage.setItem('data',JSON.stringify(this.desserts));
        }

      
      if(localStorage.getItem('data') != null){
          this.desserts = JSON.parse(localStorage.getItem('data'));
        }
        onload = this.d1 = true;

setInterval(() => {
